*
{
    margin: 0;
    padding: 0;
}


html,
body
{
    height: 100vh;
    background: linear-gradient(180deg, #4b75e8, #54c0ff);
    font-family: 'Yport';
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

$ease-out-quart: cubic-bezier(0.25, 1, 0.5, 1);

.flex-parent {
  display: flex;
}

.flex-parent > * {
 
}


#cursor {
  position: fixed;
  width: 39px;
  height: 39px;
  border: 1px solid white;
  border-radius: 39px;
  transition: all .35s $ease-out-quart;
  pointer-events: none;
  display: none;
  
  .cursorH {
    width: 39px;
    height: 0px;
    position: absolute;
    top: 20px;
    border-top: 1px solid white;
  }
  .cursorV {
    width: 0px;
    height: 39px;
    position: absolute;
    left: 20px;
    border-right: 1px solid white;
  }
}

@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}


.label {
  position: absolute;
  padding: 4px 8px;
  //pointer-events: none;
  opacity: 1;
  transition: opacity .5s;
  color: white;
  font-weight: 600;
  display: grid;
  grid-template-areas:
      "item1 item1 item1"
      "item2 item3 item4"
      "item5 item5 item5";
  grid-template-columns: 84px 22px 84px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  
  &.active {
    opacity: 1;
  }

  
  .label-title {
    max-width: 180px;
    text-align: center;
    font-size: 1.4rem;
    letter-spacing: .01rem;
    grid-area: item1;
    margin-bottom: 8px;
    justify-self: center;
    line-height: 1.15;
    transition: transform .45s $ease-out-quart;
    transform: translateY(50px);
    //transition-delay: .02s;
  }
  
  .label-leftArrow {
    background-image: url('./img/label-arrow-left.svg');
    height: 30px;
    width: 33px;
    background-repeat: no-repeat;
    background-size: contain;
    grid-area: item2;
    justify-self: end;
    transition: transform .45s $ease-out-quart;
  }
  
  .label-ball {
    
    height: 96px;
    width: 96px;
    grid-area: item3;
    justify-self: center;
    //box-sizing: border-box;
    transition: all .45s $ease-out-quart;
    transform: scale(0.18);
    //background-color: red;
    border-radius: 120px;
    border: 24px solid white;
    
    
    img {
     height: 96px;
     width: 96px;
      image-fit: cover;
      border-radius: 120px;
      // border: 24px solid white;
      box-sizing: border-box;
      opacity: 0;
      transition: all .45s $ease-out-quart;
    }
  }
  
  .label-rightArrow {
    background-image: url('./img/label-arrow-right.svg');
    height: 30px;
    width: 33px;
    background-repeat: no-repeat;
    background-size: contain;
    grid-area: item4;
    justify-self: start;
    transition: transform .45s $ease-out-quart;
  }
  
  .label-author {
    max-width: 180px;
    text-align: center;
    font-size: 1rem;
    letter-spacing: 0.01rem;
    grid-area: item5;
    margin-bottom: 8px;
    justify-self: center;
    line-height: 1.15;
    transition: all 0.45s $ease-out-quart;
    transform: translateY(-40px);
    opacity: 0;
    pointer-events: none;
  }
  
  &:hover {
    .label-title {
      transform: scale(1.25) translateY(-20%);
    }
    .label-leftArrow {
      transform: translateX(-145%);
    }
    .label-rightArrow {
      transform: translateX(145%);
    }
    .label-ball {
      transform: scale(1);
      border: 6px solid white;
      
      img {
        //border: 6px solid white;
        opacity: 1;
        // transform: translate(-25%, -25%);
      }
    }
    .label-author {
      transform: translateY(10px);
      opacity: 1;
    }
  }
}

.hover {
      opacity: 1;
}




// MOON NAVIGATION

.letter {
  animation-name: fade-in-up;
  animation-duration: 2s;
  animation-fill-mode: forwards;
  opacity: 0;
  transform: translateY(100px);
}

.button-enter {
  width: 80px;
  height: 80px;
  // border: 1px solid white;
  // border-radius: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  
  svg, div {
    position: absolute;
  }
}

 @keyframes drawCircle {
  to {
    stroke-dashoffset: 0;
  }
}



.moon-nav-big-preview {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  pointer-events: none;
  transition: all 1s;
  
  .title {
    max-width: 40vw;
    font-size: 5vw;
    margin-bottom: 0;
  }
  
  .attribution {
    padding-top: 20px;
    padding-bottom: 30px;
  }
  
  &.active {
    opacity: 1;
    pointer-events: all;
    
   .attribution {
     animation-name: fade-in-up;
     animation-duration: 1.2s;
     animation-fill-mode: forwards;
     animation-delay: 1s;
     opacity: 0;
     //transform: translateY(100px);
   }
   
   .button-enter {
     animation-delay: 1.5s;
   }
   #myCircle {
     stroke-dasharray: 251;
     stroke-dashoffset: 251;
     animation: drawCircle 2s linear forwards;
   }
  }
}


.moon-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 30px;
  left: 50vw;
  transform: translateX(-50%);
}

.moon-nav-block {
  min-width: 15px;
  min-height: 40px;
  padding-left: 2px;
  padding-right: 2px;
  cursor: pointer;
  
  div {
    height: 40px;
    width: 15px;
    background-repeat: no-repeat;
    // background-size: contain;
    position: absolute;
    opacity: 0;
    transition: opacity .2s, width .25s $ease-out-quart
    //border: 1px solid red;
  }
  
  .moon-nav-left-unfilled {
    background-image: url('./img/moon-unfilled-left.svg');
  }
  .moon-nav-left-filled {
    background-image: url('./img/moon-filled-left.svg');
  }
  .moon-nav-center {
    background-image: url('./img/moon-center.svg');
    
    &:after {
     content: "";
     display: block;
     width: 0px;
     height: 0px;
     border-radius: 15px;
     border: 5.5px solid white;
     position: absolute;
     left: 9px;
     top: 10px;
      
    }
  }
  .moon-nav-right-unfilled {
    background-image: url('./img/moon-unfilled-right.svg');
  }
  .moon-nav-right-filled {
    background-image: url('./img/moon-filled-right.svg');
  }
}


