.moon-state-left-filled,
.moon-state-left-unfilled,
.moon-state-right-filled,
.moon-state-right-unfilled,
 {
  &-1 {
      opacity: 1;
  }
  &-2 {
      opacity: .7;
  }
  &-3 {
      opacity: .4;
  }
}

.moon-state-center {
  width: 30px;
  .moon-nav-center {
    width: 30px;
    opacity: 1;
  }
}

[class*='moon-state-left-filled'] {
  .moon-nav-left-filled {
    opacity: 1;
  }
}
[class*='moon-state-left-unfilled'] {
  .moon-nav-left-unfilled {
    opacity: .4;
  }
}
[class*='moon-state-right-filled'] {
  .moon-nav-right-filled {
    opacity: 1;
  }
}
[class*='moon-state-right-unfilled'] {
  .moon-nav-right-unfilled {
    opacity: .4;
  }
}

.moon-nav-story {
  color: white;
  font-size: 1.6rem;
  line-height: 1.1;
  font-weight: 600;
  position: absolute;
  height: 90px;
  text-align: center;
  transition: all .5s $ease-out-quart;
  width: 160px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.hidden {
  opacity: 0;
  pointer-events: none;
}




.story-container {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 100;
  display: flex;
  padding: 20px 0px;
  overflow-y: scroll;
  opacity: 0;
  pointer-events: none;
  transition: opacity .5s;
  display: flex;
  justify-content: center;
  
  &.active {
    opacity: 1;
    pointer-events: all;
  }

  
  .button-back {
    display: block;
    color: white;
    width: 60px;
    height: 60px;
    border: 1px white solid;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    cursor: pointer;
    font-weight: 600;
    font-size: .8rem;
    letter-spacing: .1rem;
    transition: background-color .25s;
    
    position: sticky;
    top: 0;
    margin-right: 58px;
    
    &:hover {
      background-color: rgba(255, 255, 255, 0.3)
    }
  }
  
  #story-load-container {
    margin-right: 120px;
    
  }
}

.story-overlay {
  background-color: black;
  opacity: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  z-index: 1;
  pointer-events: none;
  transition: opacity .5s;
  
  &.active {
    opacity: .7
    // pointer-events: none;
  }
}

.story-title-block {
  padding-top: 22px;
  
  h1 {
    font-size: 5vw;  
    margin-bottom: 100px;
  }
  
  h2 {
    padding-bottom: 40px;
  }
}

.story-intro-block {
  display: grid;
  grid-template-columns: 1fr 1fr; 
  gap: 60px;
  
  
  
  img {
    width: 100%;
  }
  
  blockquote {
    font-size: 1.8rem;
    margin-bottom: 60px;
  }
  
  figcaption {
    @include t_4;
    margin-top: 20px;
  }
}

.story-block {
  display: grid;
  grid-template-columns: 1fr 1fr; 
  gap: 60px;
  
  img {
    width: 100%;
  }
  
}

.audio-clip {
  border: 1px solid white;
  border-radius: 20px;
  width: auto;
  padding: 14px 16px;
  width: max-content;
  background-color: rgba(255,255,255,.2);
  
  .title {
    @include t_4;
    margin-bottom: 0;
  } 
  .subtitle {
    @include t_5;
  } 
}

.story-content-block {
  padding-bottom: 100px;
}

.story-title-block, .story-content-block {
  z-index: 1;
  width: 60vw;
  
  h1, h2, h3, h4, p, blockquote, figcaption, ul, ol {
    color: white;
  }
}

p {
  font-size: 1.2rem;
  margin-bottom: 1.2rem;
  line-height: 1.4;
}

h1 {
  font-size: 5.4rem;
  line-height: .95;
  margin-left: -2px;
  margin-bottom: 2.4rem;
  font-weight: 600;
  letter-spacing: -0.02rem;
}

h2 {
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: .1rem;
  
}
